@media (max-width: 767px) {
  .gallery__list {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, auto);
    grid-gap: 20px;
  }
}
.gallery__item--big {
  grid-column: 2/3;
  grid-row: 1/3;
}
.gallery__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 1023px) {
  .catalog-filter {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    opacity: 0;
    visibility: hidden;
    transform: translateX(-120%);
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease 1s;
    z-index: 30;
  }
  .catalog-filter.is-open {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    transition: transform 0.3s ease, opacity 0.3s ease;
  }
}
@media (max-width: 1023px) {
  .catalog-filter__wrapper {
    position: relative;
    width: 40%;
    height: 100%;
    background-color: #ffffff;
  }
}
@media (max-width: 767px) {
  .catalog-filter__wrapper {
    width: 80%;
  }
}
@media (max-width: 1023px) {
  .catalog-filter__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
.catalog-filter__btn-close {
  display: none;
}
@media (max-width: 1023px) {
  .catalog-filter__btn-close {
    position: absolute;
    top: 0;
    right: -32px;
    display: block;
  }
}
.catalog-filter__btn-close .btn {
  padding: 6px;
}
.catalog-filter__accordion {
  margin-bottom: 20px;
}
@media (max-width: 1023px) {
  .catalog-filter__submit, .catalog-filter__reset {
    padding: 0 40px;
  }
}
.catalog-filter__submit:not(:last-child), .catalog-filter__reset:not(:last-child) {
  margin-bottom: 20px;
}
.catalog-filter__submit .btn, .catalog-filter__reset .btn {
  justify-content: center;
  width: 100%;
  padding: 25px;
}

.catalog {
  overflow: hidden;
}
.catalog .container {
  display: grid;
  grid-template-columns: 245px 1fr;
  grid-column-gap: 50px;
  padding-top: 80px;
  padding-bottom: 40px;
}
@media (max-width: 1023px) {
  .catalog .container {
    grid-template-columns: 1fr;
  }
}
.catalog__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .catalog__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
.catalog__btn-filter {
  display: none;
}
@media (max-width: 1023px) {
  .catalog__btn-filter {
    display: block;
    margin-bottom: 50px;
  }
}
.catalog__btn-filter .btn {
  justify-content: center;
  width: 100%;
  padding: 25px;
}
.catalog__content {
  position: relative;
}
.catalog__content::after {
  content: "";
  position: absolute;
  left: -1000%;
  bottom: 0;
  width: 10000%;
  height: 1px;
  background-color: #f6f5f4;
}
.catalog__wrapper {
  padding-bottom: 50px;
}
.catalog__controls {
  grid-column: 2/3;
  display: flex;
  justify-content: space-between;
  padding: 35px 0;
}